  ngOnInit() {
    gsap.registerPlugin(ScrollTrigger, Draggable);
    this.scrollingAbout();
  }
  scrollingAbout() {
    document.querySelectorAll('.scrollA').forEach( scrollA => {
      const cardScroll = gsap.timeline( {
        scrollTrigger: {
          trigger: scrollA,
          start: 'top bottom',
          end: 'bottom bottom',
          toggleActions: 'play none none reverse',
        }
      });
      cardScroll.from( scrollA , 1, {   translateY: 100, opacity: 0 , });
      cardScroll.to( scrollA , 1, {  translateY: 0, opacity: 1 , });
    });
  }
