import {Component, EventEmitter, OnInit, Output, Pipe, PipeTransform} from '@angular/core';
import {Lightbox, LightboxConfig} from 'ngx-lightbox';
import { LightboxEvent, LIGHTBOX_EVENT } from 'ngx-lightbox';
import { Subscription } from 'rxjs';
import {gsap} from 'gsap/gsap-core';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Draggable from 'gsap/Draggable';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
  // tslint:disable-next-line:variable-name
  constructor(private _lightBoxConfig: LightboxConfig, private _lightBox: Lightbox) {
    for (let i = 1; i <= 4; i++) {
      const src = 'assets/fitness/photo-' + i + '.png';
      const caption = 'Image ' + i + ' caption here';
      const thumb = 'assets/fitness/photo-' + i + '.png';
      const album = {
        src: src,
        caption: caption,
        thumb: thumb
      };
      this._albums.push(album);
    }
  }

  public gallery = [
    {value: 'Videos', img: 'assets/fitness/video-1.png' },
    {value: 'Photos' , img: 'assets/fitness/photo-1.png' },
    {value: 'Photos', img: 'assets/fitness/photo-2.png' },

    {value: 'Photos' , img: 'assets/fitness/photo-3.png' },
    {value: 'Photos', img: 'assets/fitness/photo-4.png' },
    {value: 'Videos', img: 'assets/fitness/video-2.png' },

  ];
  public SelectedValue = 'All';
  public All = this.gallery.length;
  filter = 'All';
  displayOption;
  selectedItem = 0;
  global; ['window'] = window;
  public Photos = this.gallery.filter( x => x.value === 'Photos').length;
  public Videos = this.gallery.filter( x => x.value === 'Videos').length;
  // tslint:disable-next-line:variable-name
  // @ts-ignore
  private _albums: Array = [];
  onValueChange(selectedValue: string) {
    this.SelectedValue = selectedValue;
  }
  /*open( index: number): void {
    // open lightbox
    this._lightBox.open(this._albums, index, {
      wrapAround: true,
      showImageNumberLabel: true,
      disableScrolling:	false,
      enableTransition: true,
      fitImageInViewPort:	true,

    });
  }
*/
  close(): void {
    // close lightbox programmatically
    this._lightBox.close();
  }
  /*public gImages = [
    { value: 'Videos', img: 'assets/fitness/video-1.png' },
    { value: 'Photos', img: 'assets/fitness/video-1.png' },
];

  itemsFiltered = this.gallery;

  filterItems(filterBy) {
    if (filterBy === 'All') {
      this.itemsFiltered = this.gallery;
    } else {
      this.itemsFiltered = this.gallery.filter(m => m.value === filterBy);
    }
  }*/
  onChange(value) {
    this.filter = value;
  }
  ngOnInit() {
    gsap.registerPlugin(ScrollTrigger, Draggable);
    this.scrollingGallery();
    this.scrollingImg();
  }
  scrollingGallery(): void {
    document.querySelectorAll('.scrollG').forEach( scrollG => {
      const cardScroll = gsap.timeline( {
        scrollTrigger: {
          trigger: scrollG,
          start: 'center bottom',
          end: 'bottom bottom',
          toggleActions: 'play none none reverse',
        }
      });
      cardScroll.from( scrollG , 1, {  translateY: 50, opacity: 0 , stagger: 0.1235 });
      cardScroll.to( scrollG , 1, { translateY: 0, opacity: 1 , stagger: 0.1235 });
    });
  }

  scrollingImg(): void {
    document.querySelectorAll('.g_images').forEach( scrollG => {
      const cardScroll = gsap.timeline( {
        scrollTrigger: {
          trigger: scrollG,
          start: 'center bottom',
          end: 'bottom bottom',
          toggleActions: 'play none none reverse',
        }
      });
      cardScroll.from( scrollG , 1, {  translateY: 50, opacity: 0 , stagger: 0.1235 });
      cardScroll.to( scrollG , 1, { translateY: 0, opacity: 1 , stagger: 0.1235 });
    });
  }
}
