  ngOnInit() {
    gsap.registerPlugin(ScrollTrigger, Draggable);
    this.isAnimation();
  }
  isAnimation(): void {
    document.querySelectorAll('.card').forEach( card => {
     const cardScroll = gsap.timeline( {
       scrollTrigger: {
         trigger: card,
         start: 'center bottom',
         end: 'bottom bottom',
         toggleActions: 'play none none reverse',
       }
     });
     cardScroll.from( card , { duration: 1,  translateY: 50, opacity: 0 , });
     cardScroll.to( card , { duration: 1, translateY: 0, opacity: 1 , });
    });
    }
