import {ChangeDetectorRef, Component, ElementRef, HostListener, OnInit, Renderer} from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import 'gsap';
import { TimelineMax } from 'gsap';
import Draggable from 'gsap/Draggable';
import ScrollTrigger from 'gsap/ScrollTrigger';
import {gsap} from 'gsap/gsap-core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'index',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
  animations: [
    trigger('toggleBox', [
      state('open', style({
        width: '300px',
        opacity: 1,
      })),
      state('closed', style({
        width: '0',
        opacity: 0,

      })),
      transition('open => closed', [
        animate('5s')
      ]),
      transition('closed => open', [
        animate('1s')
      ]),
    ])
  ]
})
export class MainComponent implements OnInit {
  loading = new TimelineMax();
  menu = new TimelineMax({paused: true, reversed: true});
  homeA = new TimelineMax();
  logoA = new TimelineMax();
  isOpen;
  visible = false;
  toggle() {
    this.visible = !this.visible;
  }
  // nav bar and home begin //
  public img = 'assets/fitness/home-bg.png';
  public img2 = 'assets/fitness/home-bg-4.png';

  // home begin //
  public navList = [
    {navItem: 'Home'}, {navItem: 'About'}, {navItem: 'Benefits'}, {navItem: 'Gallery'}, {navItem: 'Contact'},
  ];
  public location = 'Hyderabad';
  public mobile = 987654321;
  public nowPayText = 'Now pay per use & gym anytime, anywhere!';
  // about begin //
  public about = [
    {
      title: 'About fitness',
      p_text: 'Lorem ipsum, or lipsum as it is sometimes known, is dummy text used in laying out print,' +
        ' graphic or web designs. The passage is attributed to an unknown typesetter in the 15th century who is thought' +
        ' to have scrambled parts of Cicero\'s De Finibus Bonorum et Malorum for use in a',
      name: 'Rohit Chattopadhyay',
      img_url: 'assets/gym/image-01.png',
    },
  ];

   refresh(): void {
    window.location.reload();
  }
  // nav bar and home end //
  constructor(private el: ElementRef, private renderer: Renderer) {
  }

  ngOnInit() {
    this.createMenuAnim();
    gsap.registerPlugin(ScrollTrigger, Draggable);
    this.homeLoading();
    this.logoAnimation();
    this.loadingScreen();
  }
  /*
    @HostListener('click', ['$event.target'])
    onClick(target) {
      const item = this.el.nativeElement.querySelector('li');
    }*/

  createMenuAnim() {
    this.menu.to('.sidebar', 0.5, { right: 0,  ease: 'Expo.easeInOut' });
    this.menu.to('.sb-bg', 0.2, { delay: -0.1, opacity: .25, visibility: 'visible' });
    this.menu.to('.two', { delay: 0.2, opacity: '0', x: -10, ease: 'Expo.easeInOut'}, 0);
    this.menu.to('.one', { delay: 0.2, rotation: '45', y: 5,  ease: 'Expo.easeInOut'}, 0);
    this.menu.to('.three', { delay: 0.2, rotation: '-45', y: -15, ease: 'Expo.easeInOut'}, 0);
    this.menu.to('.sidebar a',  { delay: -0.8, x: 10, y: 20, stagger: 0.12454, opacity: '0' });
    this.menu.to('.sidebar a',  0.5, { delay: -0.8, x: 0, y: 0, stagger: 0.12454, opacity: '1' });
  }
  menuClick() {
    this.menu.reversed() ? this.menu.play() : this.menu.reverse();
  }
  menuClose() {
    this.menu.reverse();
  }
  // home and nav bar begin //
  @HostListener('window:scroll', ['$event'])
  onWindowScroll(e) {
    const element = document.querySelector('nav');
    if (window.pageYOffset > element.clientHeight) {
      element.classList.add('navbar-inverse');
    } else {
      element.classList.remove('navbar-inverse');
    }
  }
  // home and nav bar end //

  homeLoading() {
    this.homeA.from('.wrp img', 0.5, { opacity:  0});
    this.homeA.to('.wrp img', 0.5, { opacity:  1});
    this.homeA.from('.title-text-home ', 1.5, { opacity: 0, translateY: 30,  ease: 'Expo.easein' });
    this.homeA.to('.title-text-home ', 1.5, { opacity: 1, translateY: 0,  ease: 'Expo.easein' });
    this.homeA.to('.nav-link, .logo', 1.5, { translateY: 0, opacity: 1, ease: 'Expo.easeInOut', stagger: 0.1235 }, -.8);
    this.homeA.play();
  }
  logoAnimation() {
    this.logoA.from(' .logo', .7, { opacity: 0, translateY: -100 });
    this.logoA.to(' .logo', .7, { opacity: 1, translateY: 0 });
    this.logoA.play();
  }
  loadingScreen() {
    this.loading.to('.loading', { opacity: 1 });
    this.loading.to('.loading', { opacity: 0, visibility: 'hidden' });
    this.loading.play();
  }
}
