import { Component, OnInit } from '@angular/core';
import {gsap} from 'gsap/gsap-core';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Draggable from 'gsap/Draggable';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    gsap.registerPlugin(ScrollTrigger, Draggable);
    this.scrollingContact();
  }
  scrollingContact(): void {
    document.querySelectorAll('.scrollContact').forEach( scrollC => {
      const cardScroll = gsap.timeline( {
        scrollTrigger: {
          trigger: scrollC,
          start: 'center bottom',
          end: 'bottom bottom',
          toggleActions: 'play none none reverse',
        }
      });
      cardScroll.from( scrollC , 1, {  translateY: 50, opacity: 0 , stagger: 0.1235 });
      cardScroll.to( scrollC , 1, { translateY: 0, opacity: 1 , stagger: 0.1235 });
    });
  }
}
