import { Component, OnInit } from '@angular/core';
import { gsap } from 'gsap';
import Draggable from 'gsap/Draggable';
import ScrollTrigger from 'gsap/ScrollTrigger';
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'benefits',
  templateUrl: './benefits.component.html',
  styleUrls: ['./benefits.component.css']
})
export class BenefitsComponent implements OnInit {
    public title = 'Benefits';
  public tagLine = 'We make it easy and affordable for you to stay fit and healthy';
public benefits = [
  {
    icon: 'fas fa-dumbbell',
    name: 'State of art equipments',
    des: 'Modern conveniences & functional spacesto help you gain from workout',
    lMore: 'Learn More'
  },

  { icon: 'fas fa-wallet',
    name: 'Pay Per Use',
    des: 'Pay only for the number of hours you workout in gym', lMore: 'Learn More'
  },
  { icon: 'fas fa-running',
    name: 'CERTIFIED EXPERTS',
    des: 'Certified experts and trainers to help you acheive your fitness goals',
    lMore: 'Learn More',
  },
];
  constructor() { }

  ngOnInit() {
    this.scrollingBenefitsText();
  }

  scrollingBenefitsText() {
    document.querySelectorAll('.scrollB').forEach(scrollB => {
      const cardScroll = gsap.timeline({
        scrollTrigger: {
          trigger: scrollB,
          start: 'top bottom',
          end: 'bottom bottom',
          toggleActions: 'play none none reverse'
        }
      });
      /*cardScroll.from( scrollB , 1, {  translateY: 50, opacity: 0 , stagger: .1235 });*/
      cardScroll.to(scrollB, {duration: 1.5, translateY: 0, opacity: 1});
    });
  }
}
